import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { Hidden, Toolbar } from '@mui/material';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import MenuIcon from '@mui/icons-material/Menu';
import { alpha } from '@mui/material/styles';
import { useState } from 'react';
import Stack from '@mui/material/Stack';
import { ChakraProvider } from '@chakra-ui/react'

import { ChevronUpIcon } from '@heroicons/react/24/solid'

function Themes() {
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });
  const flexContainer = {
    display: 'flex',
    flexDirection: 'row',
    padding: 0,
    left: 10,

  };

  function Winter(color) {
    document.body.style.backgroundImage = "linear-gradient(62deg, #8EC5FC, #E0C3FC )";
    document.body.style.backgroundSize = "400% ";
  }
  function Summer(color) {
    document.body.style.backgroundImage = "linear-gradient(43deg, #4158d0, #c850c0,#ffcc70 )";
    document.body.style.backgroundSize = "400% ";

  }

  function Autumn(color) {
    document.body.style.backgroundImage = "linear-gradient(43deg, #FBAB7E, #F7CE68 )";
    document.body.style.backgroundSize = "400% ";
  }





  const [show, setShow] = useState(false);

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  const list = (anchor) => (

    <Box className='box'
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}

    >
      <h1 className="settings" >
        Themes
      </h1>
      <Stack className="list" direction="row" spacing={20} >

        <Button onClick={Summer} sx={{ background: 'linear-gradient(to right bottom, #4158d0, #c850c0,#ffcc70 )', width: 400, height: 200 }} variant="contained" className="">Summer</Button>

        <Button onClick={Winter} sx={{ background: 'linear-gradient(to right bottom, #D9AFD9, #97D9E1 )', width: 400, height: 200 }} variant="contained" className="button">Winter</Button>



        <Button onClick={Autumn} sx={{ background: 'linear-gradient(to right bottom, #FBAB7E, #F7CE68 )', width: 400, height: 200 }} variant="contained" className="button">Autumn</Button>

      </Stack>


    </Box >

  );
  return (
    <div>
      {
        ['bottom'].map((anchor) => (
          <React.Fragment key={anchor}>
            <Button onClick={toggleDrawer(anchor, true)}>
              <ChevronUpIcon color='white' />
            </Button>
            <Drawer
              anchor={anchor}
              open={state[anchor]}
              onClose={toggleDrawer(anchor, false)}
              PaperProps={{
                sx: { color: 'white', bgcolor: alpha('#000000', 0.1), height: '400px' },
              }}
            >
              {list(anchor)}
            </Drawer>
          </React.Fragment>
        ))
      }
    </div >
  );
}

export default Themes;
