import Cookies from 'universal-cookie';
import { useEffect, useState } from 'react';
import { useGeolocated } from "react-geolocated";


const cookies = new Cookies();

function firstInterval(f, m, ...p) {
  const h = setInterval(() => { f.apply(null, p) }, m);
  f.apply(null, p);
  return h;
}
function Weather() {
  const [chars, setChars] = useState(0);
  const [apiUrl, setApiUrl] = useState("");
  const [city, setCity] = useState("");
  const [icon, setIcon] = useState("");

  useEffect(() => {
    const interval = firstInterval(() => {

      navigator.geolocation.getCurrentPosition(function (position) {
        console.log("Latitude is :", position.coords.latitude);
        console.log("Longitude is :", position.coords.longitude);
        const apiKey = 'a4642a09d0d3f24f43b40c23fcb5f403';
        const lat = position.coords.latitude;
        const lon = position.coords.longitude;
        setApiUrl(`https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${apiKey}&units=metric`);

      });
      fetch(apiUrl).then(res => res.json()).then(data => {
        setChars(data.main.temp);
        setCity(data.name);
        const iconID = data.weather[0].icon;
        setIcon(`http://openweathermap.org/img/wn/${iconID}.png`);



      });

    }, 300000);

    return () => clearInterval(interval);
  }, [apiUrl]);
  try {
    return chars + "C"

  } catch {
    return "Loading...";
  }

}
export default Weather;

